const optionInit = (data) => {
  const categories = [];

  for (let i = 0; i < data.length; i += 1) {
    categories.push(data[i].name);
  }

  const options = {
    chart: {
      type: 'bar',
      height: 350,
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: '55%',
        endingShape: 'rounded',
      },
    },
    dataLabels: {
      enabled: false,
    },
    stroke: {
      show: true,
      width: 2,
      colors: ['transparent'],
    },
    xaxis: {
      categories,
    },
    yaxis: {
      title: {
        // text: '$ (thousands)',
      },
    },
    fill: {
      opacity: 1,
    },
    // tooltip: {
    //   y: {
    //     formatter: function (val) {
    //       return '$ ' + val + ' thousands';
    //     },
    //   },
    // },
  };
  return options;
};

const seriesInit = (data) => {
  const soc = [];
  const temp = [];

  for (let i = 0; i < data.length; i += 1) {
    soc.push(data[i].soc);
    temp.push(data[i].temp);
  }
  const series = [
    {
      name: '배터리 용량',
      data: soc,
    },
    {
      name: '배터리 온도',
      data: temp,
    },
  ];
  return series;
};

export { optionInit, seriesInit };
